button,
.button {
  position: relative;
  cursor: pointer;
  overflow: hidden;
  border-radius: calc(infinity * 1px);
  background-color: var(--foreground);
  padding-inline: 15px;
  padding-top: 10px;
  padding-bottom: 6;
  --tw-font-weight: var(--font-weight-medium);
  font-weight: var(--font-weight-medium);
  color: var(--background);
  text-transform: uppercase;
}
.button {
  display: inline-block;
}
.tq__button__text,
.tq__button__layer-text {
  position: relative;
  transition: top 0.4s cubic-bezier(0.33, 1, 0.68, 1);
}
.tq__button__text {
  top: calc(var(--spacing) * 0);
}
.tq__button__layer {
  position: absolute;
  top: 110%;
  left: calc(var(--spacing) * 0);
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  border-radius: calc(infinity * 1px);
  transition: top 0.4s cubic-bezier(0.33, 1, 0.68, 1);
}
.tq__button__layer .tq__button__layer-text {
  position: absolute;
  color: var(--color-white);
}
.tq__button__layer .tq__button__layer-background {
  height: 100%;
  width: 100%;
  border-radius: calc(infinity * 1px);
  background-color: var(--color-gray-500);
  transition: all 0.4s cubic-bezier(0.33, 1, 0.68, 1);
}
.tq__button:hover {
  background-color: var(--color-black);
  transition: all 0.4s cubic-bezier(0.33, 1, 0.68, 1);
}
.tq__button:hover .tq__button__text,
.tq__button__text:hover {
  top: -40px;
}
.tq__button:hover .tq__button__layer,
.tq__button__text:hover + .tq__button__layer {
  top: calc(var(--spacing) * 0);
}
.tq__button__layer:hover .tq__button__layer-background,
.tq__button__text:hover + .tq__button__layer .tq__button__layer-background {
  width: 100%;
  border-radius: calc(infinity * 1px);
}
.header-blur-background {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.09);
  position: fixed;
  top: calc(var(--spacing) * 0);
  left: calc(var(--spacing) * 0);
  z-index: calc(1 * -1);
  height: 100vh;
  width: 100%;
}
.navbar-wrapper {
  position: fixed;
  bottom: calc(var(--spacing) * 12);
  left: calc(1/2 * 100%);
  z-index: 10;
  width: 700px;
  --tw-translate-x: calc(calc(2/4 * 100%) * -1);
  translate: var(--tw-translate-x) var(--tw-translate-y);
  padding-bottom: 65px;
}
.navbar {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.09);
  position: absolute;
  bottom: calc(var(--spacing) * 0);
  left: calc(var(--spacing) * 0);
  z-index: 1;
  width: 100%;
  border-radius: calc(infinity * 1px);
  border-style: var(--tw-border-style);
  border-width: 1px;
  border-color: rgba(255,255,255,0.15);
}
.navbar ul {
  display: flex;
  justify-content: center;
  padding-inline: calc(var(--spacing) * 6);
}
.navbar ul li {
  text-align: center;
}
.navbar ul li a {
  display: inline-block;
  width: 100%;
  padding-inline: calc(var(--spacing) * 5);
  padding-block: calc(var(--spacing) * 6);
}
.navbar-open {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.09);
  margin-bottom: calc(65px * -1);
  display: flex;
  width: 100%;
  align-items: center;
  gap: calc(var(--spacing) * 6);
  border-radius: var(--radius-2xl);
  border-bottom-right-radius: 35px;
  border-bottom-left-radius: 35px;
  border-style: var(--tw-border-style);
  border-width: 1px;
  border-color: rgba(255,255,255,0.15);
  padding-top: 32px;
  padding-bottom: 97px;
}
.navbar-open ul {
  width: 100%;
  padding-inline: calc(var(--spacing) * 10);
}
.navbar-open ul li {
  width: 100%;
}
.navbar-open ul li a {
  display: inline-block;
  width: 100%;
  border-top-style: var(--tw-border-style);
  border-top-width: 1px;
  border-bottom-style: var(--tw-border-style);
  border-bottom-width: 1px;
  border-color: rgba(255,255,255,0.15);
  padding: calc(var(--spacing) * 3);
}
.navbar-open h3 {
  width: 100%;
}
